import React, { useEffect, useState } from 'react';
import {
  Button,
  Modal,
  ModalVariant,
  Form,
  FormGroup,
  Popover,
  TextInput,
  FormSelect,
  FormSelectOption,
  Flex,
  FlexItem,
  Card,
  CardBody,
  Bullseye,
  List,
  ListItem,
  Title,
  Text,
  TextVariants,
} from '@patternfly/react-core';
import { HelpIcon, ExclamationCircleIcon, CheckCircleIcon } from '@patternfly/react-icons';
import { useKeycloak } from '@react-keycloak/web';
import { OrganizationRepresentation, OrganizationRoleRepresentation } from 'src/query/orgs';
import { useForm, Controller } from 'react-hook-form';
import { baseUrl } from 'src';
import toast from 'react-hot-toast';
import { nanoid } from 'nanoid';
import { keyBy } from 'lodash';
import { API } from 'src/api';

type Props = {
  refresh: (val: string) => void;
  isModalOpen: boolean;
  setModalOpen: (open: boolean) => void;
};

type Role = {
  id: string;
  name: string;
};

type OrgWithRole = {
  orgId: OrganizationRepresentation['id'];
  roles: Role[];
};

const regions = [
  {
    id: 'us-west-2',
    displayName: 'us-west-2',
  },
  {
    id: 'eu-central-1',
    displayName: 'eu-central-1',
  },
];

export const CreateDeployment: React.FC<Props> = ({ refresh, isModalOpen, setModalOpen }) => {
  const { keycloak } = useKeycloak();
  const {
    handleSubmit,
    formState: { errors, isSubmitting },
    control,
    reset,
  } = useForm();

  const [orgs, setOrgs] = useState<OrganizationRepresentation[]>([]);
  const [orgsWithRole, setOrgsWithRole] = useState<OrgWithRole[]>([]);

  const getUserOrgs = async () => setOrgs(await API.User.getUserOrgs());

  const checkRoles = async () => {
    const roleCheck: OrgWithRole[] = await Promise.all(
      orgs.map(async (org) => ({ orgId: org.id, roles: await API.User.getUserOrgRoles(org.id) }))
    );
    setOrgsWithRole(roleCheck);
  };

  useEffect(() => {
    checkRoles();
  }, [orgs]);

  useEffect(() => {
    if (isModalOpen) {
      getUserOrgs();
    }

    if (isModalOpen === false) {
      reset();
    }
  }, [isModalOpen]);

  const handleModalToggle = () => {
    setModalOpen(!isModalOpen);
  };

  const submitForm = async (dep) => {
    try {
      const createDeployment = await fetch(`${baseUrl}/${keycloak.realm}/deployments`, {
        method: 'POST',
        headers: { Authorization: `Bearer ${keycloak.token}`, 'Content-Type': 'application/json' },
        body: JSON.stringify({
          name: dep.name,
          organization_id: dep.organization,
          region: dep.region,
        }),
      });

      if (createDeployment.ok) {
        toast.success(`${dep.name} deployment created successfully.`);
        setModalOpen(false);
        refresh(dep.name + nanoid());
        reset();
        return;
      }
      const errorResp = await createDeployment.json();
      toast.error(`${dep.name} deployment was not able to be created. (${errorResp.error})`);
    } catch (e) {
      console.log('[Error] Not able to create the deployment.');
    }
  };

  const hasError = (key: string) => (errors[key] ? 'error' : 'default');

  // Filter only orgs that user has the right role for
  const roleCheckMap = keyBy(orgsWithRole, 'orgId');
  const manageRoleOrgs = orgs.filter((org) =>
    roleCheckMap[org.id!]?.roles.find((role) => role.name === 'manage-organization')
  );

  const SelectionOption = ({ title, subtitle, listItems }) => (
    <FlexItem flex={{ default: 'flex_1' }} alignSelf={{ default: 'alignSelfStretch' }}>
      <Card style={{ height: '100%' }}>
        <CardBody>
          <Bullseye style={{ flexDirection: 'column', justifyContent: 'flex-start' }}>
            <Title headingLevel="h2" style={{ marginBottom: '1rem' }}>
              {title}
            </Title>
            <Text component={TextVariants.h3} style={{ maxWidth: '70%', marginBottom: '1rem', textAlign: 'center' }}>
              {subtitle}
            </Text>
            <List isPlain iconSize="large">
              {listItems.map((li, i) => (
                <ListItem
                  icon={<CheckCircleIcon color="var(--pf-global--palette--blue-400)" style={{ width: '18px' }} />}
                  key={i}
                >
                  {li}
                </ListItem>
              ))}
            </List>
          </Bullseye>
        </CardBody>
      </Card>
    </FlexItem>
  );

  return (
    <>
      <Button variant="primary" onClick={handleModalToggle}>
        Create Deployment
      </Button>
      <Modal
        variant={ModalVariant.medium}
        title="Create Deployment"
        description="Enter details below to create a new Deployment."
        isOpen={isModalOpen}
        onClose={handleModalToggle}
        actions={[
          <Button
            key="create"
            variant="primary"
            form="modal-with-form-create-deployment"
            type="submit"
            isDisabled={isSubmitting}
          >
            {isSubmitting ? 'Creating deployment...' : 'Confirm'}
          </Button>,
          <Button key="cancel" variant="link" onClick={handleModalToggle}>
            Cancel
          </Button>,
        ]}
      >
        <Flex
          // alignContent={{ default: 'alignContentCenter' }}
          justifyContent={{ default: 'justifyContentCenter' }}
          alignItems={{ default: 'alignItemsCenter' }}
        >
          <SelectionOption
            title="Shared"
            subtitle="Single realm on our highly available clusters. Always free."
            listItems={['<1,000 users', '<5 SSO connections']}
          />
          <SelectionOption
            title="Dedicated"
            subtitle="Dedicated single-tenant clusters starting at $499/month."
            listItems={[
              <div>
                Free{' '}
                <a href="https://www.cockroachlabs.com/product/" target="_black">
                  CockroachDB
                </a>{' '}
                instance
              </div>,
              'Unlimited users',
              'Unlimited SSO connections',
            ]}
          />
        </Flex>
        <Form id="modal-with-form-create-deployment" onSubmit={handleSubmit(submitForm)}>
          <FormGroup
            label="Name"
            labelIcon={
              <Popover
                headerContent={<div>Name</div>}
                bodyContent={<div>The name of the realm. This must be unique across all Phase Two Deployments.</div>}
              >
                <button
                  type="button"
                  aria-label="More info for name field"
                  onClick={(e) => e.preventDefault()}
                  aria-describedby="modal-with-form-form-name"
                  className="pf-c-form__group-label-help"
                >
                  <HelpIcon noVerticalAlign />
                </button>
              </Popover>
            }
            isRequired
            fieldId="modal-with-form-form-name"
            validated={hasError('name')}
            helperTextInvalid={'Name is a required field. Must be at least 3 characters.'}
            helperTextInvalidIcon={<ExclamationCircleIcon />}
          >
            <Controller
              name="name"
              control={control}
              rules={{ required: true, min: 3 }}
              render={({ field }) => (
                <TextInput
                  id="modal-with-form-form-name"
                  isRequired
                  type="text"
                  validated={hasError('name')}
                  {...field}
                />
              )}
            />
          </FormGroup>
          <FormGroup
            label="Organization"
            labelIcon={
              <Popover
                headerContent={<div>Organization</div>}
                bodyContent={
                  <div>
                    This will assign the owner Organization. You must be a manager in this organization. All other
                    members of the organization will be able to fully use and administer this Deployment.
                  </div>
                }
              >
                <button
                  type="button"
                  aria-label="More info for organization field"
                  onClick={(e) => e.preventDefault()}
                  aria-describedby="modal-with-form-form-email"
                  className="pf-c-form__group-label-help"
                >
                  <HelpIcon noVerticalAlign />
                </button>
              </Popover>
            }
            isRequired
            fieldId="modal-with-form-form-email"
          >
            <Controller
              name="organization"
              control={control}
              rules={{ required: true }}
              render={({ field }) => (
                <FormSelect id="organization-choose-form" aria-label="Choose an organizaton" {...field}>
                  <FormSelectOption value="" label="Select one" isDisabled isPlaceholder />
                  {manageRoleOrgs.map((option, index) => (
                    <FormSelectOption
                      key={option.id}
                      value={option.id}
                      label={`${option.displayName} (${option.id})`}
                    />
                  ))}
                </FormSelect>
              )}
            />
          </FormGroup>
          <FormGroup
            label="Region"
            labelIcon={
              <Popover headerContent={<div>Region</div>} bodyContent={<div>Region to deploy into.</div>}>
                <button
                  type="button"
                  aria-label="More info for region feld"
                  onClick={(e) => e.preventDefault()}
                  aria-describedby="modal-with-form-form-region"
                  className="pf-c-form__group-label-help"
                >
                  <HelpIcon noVerticalAlign />
                </button>
              </Popover>
            }
            isRequired
            fieldId="modal-with-form-form-region"
          >
            <Controller
              name="region"
              control={control}
              rules={{ required: true }}
              render={({ field }) => (
                <FormSelect id="region-choose-form" aria-label="Choose a region" {...field}>
                  <FormSelectOption value="" label="Select one" isDisabled isPlaceholder />
                  {regions.map((option) => (
                    <FormSelectOption
                      key={option.id}
                      value={option.id}
                      label={`${option.displayName} (${option.id})`}
                    />
                  ))}
                </FormSelect>
              )}
            />
          </FormGroup>
        </Form>
      </Modal>
    </>
  );
};
